#menu-modal {
  position: absolute;
  top: 4.875rem;
  border: none;
  width: 100%;
  background-color: transparent;
  z-index: 1;
}

.menu-links {
  margin: 0;
  border-radius: 0.625rem;
  padding-block: 2.5rem;
  padding-inline: 1.5rem;
  list-style-type: none;
  background-color: var(--dk-violet);
}
.menu-links > *:not(:last-child) {
  margin-bottom: 2.125rem;
}
.menu-links li {
  font-size: 0.875rem;
  font-weight: 700;
  text-align: center;
}
.menu-links li hr {
  background-color: var(--gray);
}
.menu-links li a {
  width: 100%;
  color: var(--white);
  transition: all 200ms ease-in;
}
.menu-links li a:hover:not(.cta) {
  color: var(--cyan);
}
.menu-links li > .cta {
  padding-block: 1rem;
}

.cta {
  display: inline-block;
  background-color: var(--cyan);
  border-radius: 1.875rem;
  transition: opacity 300ms ease-in;
  font-weight: 700;
  color: var(--white);
}
.cta:hover {
  opacity: 0.5;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow: hidden;
  padding: 0;
  margin: 0;
  font-size: 1.125rem;
  font-weight: 500;
  color: var(--gray);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --cyan: hsl(180, 66%, 49%);
  --dk-violet: hsl(257, 27%, 26%);
  --red: hsl(0, 87%, 67%);
  --gray: hsl(0, 0%, 75%);
  --gray-violet: hsl(257, 7%, 63%);
  --very-dk-blue: hsl(255, 11%, 22%);
  --very-dk-violet: hsl(260, 8%, 14%);
}

:root {
  --font: "Poppins", sans-serif;
}

.container {
  max-width: 69.25rem;
  margin: 0 auto;
  padding-inline: 1.5rem;
}
@media (min-width: 71.875em) {
  .container {
    padding-inline: 0;
  }
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 700;
}

p {
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

.nav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 2.5rem;
  padding-bottom: 1.375rem;
}
@media (min-width: 43.75em) {
  .nav {
    padding-top: 3rem;
    padding-bottom: 4.8125rem;
    gap: 2.8125rem;
  }
}
.nav img {
  width: 7.5rem;
  height: 2.0625rem;
  cursor: pointer;
}
.nav div {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  width: 1.5rem;
  height: 1.3125rem;
  cursor: pointer;
}
@media (min-width: 43.75em) {
  .nav div {
    display: none;
  }
}
.nav div span {
  display: block;
  height: 0.1875rem;
  width: 100%;
  background-color: var(--gray-violet);
}
.nav__links {
  flex-grow: 1;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  gap: 1.875rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
  font-size: 0.875rem;
  font-weight: 700;
}
@media (max-width: 43.7em) {
  .nav__links {
    display: none;
  }
}
.nav__links li a:not(.cta) {
  color: var(--gray-violet);
  transition: all 300ms ease-in;
}
.nav__links li a:not(.cta):hover {
  color: var(--cyan);
}
.nav__links li > .cta {
  padding-block: 1rem;
}
@media (min-width: 43.75em) {
  .nav__links li > .cta {
    padding-block: 0.625rem;
    padding-inline: 1.5625rem;
  }
}
.nav__links .separator {
  flex-grow: 1;
}

.hero {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  gap: 2.8125rem;
}
@media (min-width: 43.75em) {
  .hero {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: none;
  }
}
@media (min-width: 71.875em) {
  .hero {
    gap: 6.875rem;
  }
}
.hero__image {
  position: relative;
  display: block;
  width: 100%;
  height: 21.1875rem;
}
@media (min-width: 43.75em) {
  .hero__image {
    height: 30.1875rem;
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.hero__image::before {
  content: "";
  display: block;
  height: inherit;
  position: absolute;
  top: 0;
  right: -40%;
  bottom: 0;
  left: 6%;
  background-image: url("../images/illustration-working.svg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center center;
}
@media (min-width: 43.75em) {
  .hero__image::before {
    right: -65%;
    left: 0;
  }
}
.hero__content {
  text-align: center;
}
@media (min-width: 43.75em) {
  .hero__content {
    place-self: center;
    text-align: left;
  }
}
@media (min-width: 43.75em) {
  .hero__content {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
.hero__content h1 {
  margin-bottom: 1.6875rem;
  font-size: 2.125rem;
  color: var(--black);
}
@media (min-width: 43.75em) {
  .hero__content h1 {
    margin-bottom: 1.5rem;
    font-size: 3.6875rem;
  }
}
.hero__content p {
  margin-bottom: 2.25rem;
  color: var(--gray-violet);
}
@media (min-width: 43.75em) {
  .hero__content p {
    margin-bottom: 2.75rem;
  }
}
.hero__content a {
  padding-block: 1rem;
  padding-inline: 2.5rem;
  font-size: 1rem;
}
@media (min-width: 43.75em) {
  .hero__content a {
    padding-inline: 3.4375rem;
  }
}